.container {
  display: flex;
  flex-flow: column nowrap;
  padding-bottom: 70px; }

.header {
  position: relative;
  height: 50vh;
  display: flex;
  flex-flow: column nowrap;
  align-content: center;
  justify-content: center;
  text-align: center;
  background: #0059bf url("/img/career-header.jpg") no-repeat center/cover;
  /* @2x Images (Pixel Ratio of 1.25+) */
  /* @3x Images (Pixel Ratio of 2.25+) */
  /* @4x Images (Pixel Ratio of 3.25+) */
  /* @2x Images (Pixel Ratio of 1.25+) */
  /* @3x Images (Pixel Ratio of 2.25+) */
  /* @4x Images (Pixel Ratio of 3.25+) */
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1); }
  @media only screen and (-o-min-device-pixel-ratio: 5 / 4), only screen and (-webkit-min-device-pixel-ratio: 1.25), only screen and (min-device-pixel-ratio: 1.25), only screen and (min-resolution: 1.25dppx) {
    .header {
      background-image: url("/img/career-header@2x.jpg"); } }
  @media only screen and (-o-min-device-pixel-ratio: 9 / 4), only screen and (-webkit-min-device-pixel-ratio: 2.25), only screen and (min-device-pixel-ratio: 2.25), only screen and (min-resolution: 2.25dppx) {
    .header {
      background-image: url("/img/career-header@3x.jpg"); } }
  .header::after {
    content: '';
    display: block;
    user-select: none;
    pointer-events: none;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50%;
    pointer-events: none;
    background: url(/img/wave5.svg) no-repeat bottom/contain; }
  .header h1 {
    font-size: 60px;
    font-weight: 400;
    margin: 15px 0; }
  .header p {
    font-size: 24px;
    margin: 15px 0; }

.slider {
  position: relative;
  padding: 12vw 0;
  background: #0059bf;
  background-image: linear-gradient(to right, #0059bf 0%, #00a2f0 100%);
  color: #ffffff; }
  @media screen and (max-width: 999px) {
    .slider {
      padding: 100px 0 170px 0; } }
  .slider::before {
    content: '';
    display: block;
    user-select: none;
    pointer-events: none;
    position: absolute;
    top: -1px;
    left: 0;
    width: 100%;
    height: 80%;
    background: url("/img/wave4.svg") no-repeat top/contain; }
  .slider p {
    line-height: 1.88; }

.benefits {
  padding: 100px 0;
  color: #697984;
  text-align: center; }
  .benefits__list {
    display: flex;
    flex-flow: row wrap; }
    .benefits__list > * {
      flex: 1 1 20%; }
      @media screen and (min-width: 600px) and (max-width: 999px) {
        .benefits__list > * {
          flex: 1 1 40%; } }
      @media screen and (max-width: 599px) {
        .benefits__list > * {
          flex: 1 1 100%;
          margin: 20px; } }
  .benefits h1 {
    color: #084690;
    font-size: 45px;
    font-weight: 600; }
  .benefits h2 {
    color: #017dd6; }
  .benefits img {
    height: 100px; }
  .benefits p {
    line-height: 1.88;
    width: 80%;
    margin: 0 auto; }
    @media screen and (max-width: 599px) {
      .benefits p {
        width: 100%; } }
    .benefits p:first-of-type {
      margin-bottom: 2em; }

.join {
  position: relative;
  padding: 13vw 0 100px;
  background: #0059bf;
  background-image: linear-gradient(to right, #0059bf 0%, #00a2f0 100%);
  color: #ffffff;
  text-align: center; }
  .join::before {
    content: '';
    display: block;
    user-select: none;
    pointer-events: none;
    position: absolute;
    top: -1px;
    left: 0;
    width: 100%;
    height: 80%;
    background: url("/img/wave4.svg") no-repeat top/contain; }
  .join p {
    line-height: 1.8; }



