@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* basic colors ============================================================= */
/* social colors ============================================================ */
@media screen and (max-width: 999px) {
  .container {
    display: flex;
    flex-flow: row wrap; } }

.position {
  display: block;
  padding: 30px 5px;
  transition: all 200ms ease-in-out; }
  @media screen and (max-width: 999px) {
    .position {
      margin: 0 10px;
      padding: 30px 20px; } }
  .position + .position {
    border-top: 2px solid #f2f2f2; }
    @media screen and (max-width: 999px) {
      .position + .position {
        border-top: none; } }
  .position:hover {
    text-decoration: none;
    background: rgba(0, 162, 240, 0.02); }
  .position .icon {
    float: left;
    width: 50px;
    margin: 0 10px 5px 0; }
  .position .name {
    font-size: 20px !important;
    margin: 0 !important;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: 400; }
  .position .category {
    color: #697984; }



