@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* basic colors ============================================================= */
/* social colors ============================================================ */
.container {
  color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  transition: all 200ms ease-in-out; }
  .container a {
    color: #fff;
    text-transform: uppercase;
    transition: all 200ms ease-in-out; }
    .container a:hover, .container a.active {
      text-decoration: none;
      text-shadow: 0 0 15px #fff; }
  .container__scrollOut {
    background: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3); }
    .container__scrollOut li a {
      color: #697984; }
      .container__scrollOut li a:hover, .container__scrollOut li a.active {
        text-shadow: none;
        background-color: #017dd6;
        color: #ffffff; }
        @media screen and (max-width: 599px) {
          .container__scrollOut li a:hover, .container__scrollOut li a.active {
            background-color: transparent;
            text-shadow: 0 0 15px #fff; } }

.wrapper {
  display: flex;
  flex-flow: row nowrap;
  height: 80px;
  position: relative; }
  .wrapper > * {
    flex: 1 1 100px;
    height: 100%;
    display: block; }

.logo {
  max-width: 100px;
  position: relative;
  left: -5px;
  top: 5px; }

.hamburger {
  z-index: 1000;
  position: absolute;
  top: 50%;
  right: 20px;
  width: 30px;
  height: 3px;
  display: none;
  border: none;
  background-color: #ffffff;
  transform: translateY(-50%); }
  @media screen and (max-width: 599px) {
    .hamburger {
      display: block; } }
  .hamburger::before, .hamburger::after {
    content: '';
    display: block;
    user-select: none;
    pointer-events: none;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    background-color: inherit;
    transition: all 200ms ease-in-out; }
  .hamburger::before {
    transform: translateY(-10px); }
  .hamburger::after {
    transform: translateY(10px); }
  .hamburger__open {
    background-color: transparent; }
    .hamburger__open::before {
      transform: rotate(45deg);
      background-color: #ffffff; }
    .hamburger__open::after {
      transform: rotate(-45deg);
      background-color: #ffffff; }
  .hamburger__scrollOut {
    background-color: #697984; }
  .hamburger__viewport {
    display: none; }
    @media screen and (max-width: 599px) {
      .hamburger__viewport {
        z-index: 1000;
        display: block;
        position: relative;
        border: none;
        background: none; } }



