  text-align: center;
  padding: 200px 0 100px;
  background-image: linear-gradient(to right, #0059bf 0%, #00a2f0 100%); }
  .footer::after {
    content: '';
    display: block;
    user-select: none;
    pointer-events: none;
    position: absolute;
    top: -1px;
    left: 0;
