@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* basic colors ============================================================= */
/* social colors ============================================================ */
.container {
  list-style: none;
  display: flex; }
  .container li {
    margin: 0 20px; }
    .container li a {
      color: #697984;
      font-size: 20px; }
      .container li a.Facebook:hover {
        color: #3b5998; }
      .container li a.LinkedIn:hover {
        color: #007bb6; }
      .container li a.Tweeter:hover {
        color: #00b6f1; }
      .container li a.YouTube:hover {
        color: #ff0000; }
      .container li a.Instagram:hover {
        color: #c32aa3; }
      .container li a.GooglePlus:hover {
        color: #df4a32; }
      .container li a.Skype:hover {
        color: #00aff0; }



