@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* basic colors ============================================================= */
/* social colors ============================================================ */
.button {
  cursor: pointer;
  z-index: 2;
  position: fixed;
  bottom: 60px;
  right: 10px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  background-image: url(/img/icon-top.svg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 60%;
  border: 1px solid #e0dbdb;
  transition: all 300ms cubic-bezier(0.85, 0.5, 0.35, 2); }
  .button:hover {
    transform: scale(1.2); }
  .button.hidden {
    transform: scale(0);
    pointer-events: none;
    transition-timing-function: ease; }



