@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* basic colors ============================================================= */
/* social colors ============================================================ */
.container {
  background-image: linear-gradient(to right, #0059bf 0%, #00a2f0 100%);
  height: calc(100vh - 61px);
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center; }
  .container > *:last-child {
    margin-bottom: 25vh; }

.panel {
  padding-top: 20px;
  width: 300px;
  background: #ffffff;
  border-radius: 2px; }

.form {
  color: #697984;
  padding: 0 20px 20px 20px;
  margin-top: 5px; }
  .form > * {
    display: block;
    box-sizing: border-box; }
    .form > * + * {
      margin-top: 15px; }
  .form label {
    display: block; }
    .form label .input {
      height: 40px;
      line-height: 40px;
      transition: all 200ms ease-in-out;
      font-family: inherit;
      background: none;
      border: none;
      width: 100%;
      border-bottom: 2px solid #e0e0e0;
      box-sizing: border-box;
      color: #697984;
      padding: 0 5px; }
      .form label .input:focus {
        border-color: #017dd6; }
      .form label .input::placeholder {
        text-transform: uppercase; }
      .form label .input__error {
        border-color: #f64024; }
  .form .button {
    width: 100%;
    height: 40px;
    line-height: 40px;
    background-color: #017dd6;
    color: #ffffff;
    text-align: center;
    text-transform: uppercase;
    border: none;
    border-radius: 2px;
    cursor: pointer; }
    .form .button:hover {
      background-color: #00a2f0; }
    .form .button:disabled {
      background-color: #697984; }

.error {
  height: 0;
  overflow: hidden;
  opacity: 0;
  padding: 0 5px;
  margin: 0;
  font-size: 80%;
  color: #f64024;
  user-select: none;
  transition: all 200ms ease-in-out; }
  .error__login {
    text-align: center;
    background: #feebe8;
    border-radius: 2px;
    line-height: 2em; }
  .error__active {
    opacity: 1;
    height: 2em; }



