/* social colors ============================================================ */
.clientSlider {
  max-width: 1100px;
  margin: 50px auto 0 auto;
  display: flex;
  flex-flow: row nowrap; }
  @media screen and (max-width: 999px) {
    .clientSlider {
      flex-direction: column; } }
  .clientSlider a {
    color: #017dd6;
    transition: all 100ms ease-in-out; }
    .clientSlider a:hover {
      text-decoration: none;
      color: #16de99; }
  .clientSlider .clients {
    flex-basis: 300px;
    min-width: 300px; }
    @media screen and (max-width: 999px) {
      .clientSlider .clients {
        flex-basis: 0;
        min-width: 0; } }
    @media screen and (max-width: 999px) {
      .clientSlider .clients p {
        text-align: center !important; } }
  .clientSlider .name {
    font-weight: bold; }
  .clientSlider .feedback {
    flex-grow: 1;
    padding-left: 20px;
    min-height: 300px; }
    .clientSlider .feedback::before, .clientSlider .feedback::after {
      content: '"';
      display: block;
      text-align: left;
      font-size: 80px;
      font-family: Tahoma;
      color: #0059bf;
      position: relative;
      top: .3em;
      pointer-events: none; }
    .clientSlider .feedback::after {
      text-align: right; }
  .clientSlider .wrapper {
    transition: all 200ms ease-in-out;
    position: relative;
    min-height: 60px;
    overflow: hidden; }
    .clientSlider .wrapper p {
      width: 100%;
      position: absolute;
      transition: all 200ms ease-in-out;
      padding: 15px 30px;
      text-align: left;
      opacity: 0;
      pointer-events: none; }
      .clientSlider .wrapper p.active {
        opacity: 1;
        pointer-events: all; }
      .clientSlider .wrapper p a:last-of-type {
        margin-left: .5em; }
  .clientSlider .name {
    display: block;
    margin-bottom: .5em; }
  .clientSlider .avatars {
    min-height: 150px;
    position: relative; }
    @media screen and (max-width: 999px) {
      .clientSlider .avatars {
        width: 133px;
        margin: 0 auto; } }
    .clientSlider .avatars .face {
      position: absolute;
      top: 0;
      transition: all 200ms ease-in-out;
      width: 133px;
      height: 133px;
      border: 6px solid white;
      background-size: contain;
      border-radius: 50%;
      cursor: pointer; }
      @media screen and (max-width: 999px) {
        .clientSlider .avatars .face {
          pointer-events: none; } }
      .clientSlider .avatars .face:hover {
        top: -20px;
        opacity: 1 !important;
        z-index: 10000000 !important;
        filter: none !important;
        border-color: #0059bf; }
        @media screen and (max-width: 999px) {
          .clientSlider .avatars .face:hover {
            top: 0; } }
      @media screen and (max-width: 999px) {
        .clientSlider .avatars .face {
          transform: none !important; } }
    .clientSlider .avatars:hover .face {
      filter: grayscale(1); }



