@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* basic colors ============================================================= */
/* social colors ============================================================ */
.loading {
  height: 200px;
  background: url(/img/loader.svg) no-repeat center center; }

table {
  width: 100%;
  margin: 20px 0; }
  table th {
    padding: 10px;
    color: #045997;
    background-color: #f2f2f2; }
  table td {
    padding: 10px;
    transition: all .1s ease-in-out; }
    table td .dev {
      color: #aa97ec; }
  table .selected td {
    background-color: #f6e4bf; }



