@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* basic colors ============================================================= */
/* social colors ============================================================ */
.button {
  position: relative;
  background: #697984;
  padding: .5em 1em .5em 3em;
  color: #ffffff;
  margin-right: 1em;
  border-radius: 4px;
  transition: all 200ms ease-in-out; }
  .button::before {
    content: '';
    display: block;
    user-select: none;
    pointer-events: none;
    position: absolute;
    display: block;
    margin-right: .5em;
    width: 1.5em;
    height: 1.5em;
    top: .35em;
    left: .5em; }
  .button:hover {
    color: #ffffff;
    text-decoration: none; }
  .button.facebook {
    background-color: #365397; }
    .button.facebook::before {
      background: url(/img/facebook.svg) no-repeat center/contain; }
    .button.facebook:hover {
      background-color: #4468bc; }
  .button.linkedin {
    background: #006db3; }
    .button.linkedin::before {
      background: url(/img/linkedin.svg) no-repeat center/contain; }
    .button.linkedin:hover {
      background-color: #008ce6; }



