@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* basic colors ============================================================= */
/* social colors ============================================================ */
.header {
  position: absolute;
  top: 0;
  text-align: center;
  padding: 15vh 0 10vw 0;
  background: url(/img/wave1.svg) no-repeat bottom/contain, linear-gradient(to right, #0059bf 0%, #00a2f0 100%);
  height: 350px;
  width: 100vw;
  transition: height 300ms ease-in-out; }
  .header__loading {
    padding-top: 200px;
    height: 100vh; }

.container aside {
  max-width: 300px; }
  @media screen and (max-width: 999px) {
    .container aside {
      padding-top: 50px;
      max-width: none; } }

.content {
  color: #697984;
  padding-top: calc(15vw + 15vh + 10em); }
  .content h1 {
    font-size: 45px;
    color: #084690; }
  .content p, .content ul {
    line-height: 1.88; }

.flexContainer {
  display: flex;
  flex-direction: row nowrap; }
  @media screen and (max-width: 999px) {
    .flexContainer {
      display: block; } }
  .flexContainer > * {
    padding: 0 20px;
    flex: 1 1 100px; }
    .flexContainer > *:first-child {
      flex-grow: 3; }
    .flexContainer > * h2 {
      color: #017dd6;
      font-size: 24px; }

.applyBox {
  border: 1px solid #e0e0e0;
  padding: 30px 40px;
  margin-top: 50px;
  display: flex;
  justify-content: space-between; }
  .applyBox > * {
    margin: auto 0 !important; }

.extraJob {
  background: url(/img/wave4.svg) no-repeat top/contain, linear-gradient(to right, #0059bf 0%, #00a2f0 100%);
  color: white;
  text-align: center;
  padding: 10vw 0 10vw 0;
  margin-top: 100px; }
  @media screen and (max-width: 999px) {
    .extraJob {
      padding: 100px 0 170px 0; } }
  .extraJob h1 {
    color: white; }

.backLink {
  display: block;
  height: 50px;
  line-height: 50px;
  text-align: center;
  border-radius: 5px;
  color: #017dd6;
  border: solid 1px #017dd6;
  font-size: 16px;
  font-weight: 600;
  text-transform: uppercase;
  margin-top: 30px;
  transition: all 200ms ease-in-out; }
  .backLink:hover {
    background-color: #017dd6;
    color: #ffffff;
    text-decoration: none; }



