@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* basic colors ============================================================= */
/* social colors ============================================================ */
.flexContainer {
  display: flex;
  flex-flow: row wrap; }
  .flexContainer > * {
    position: relative;
    flex: 0 0 50%;
    padding: 10px;
    box-sizing: border-box; }
    .flexContainer > *:nth-of-type(3), .flexContainer > *:nth-of-type(4), .flexContainer > *:nth-of-type(5) {
      flex: 0 0 100%; }
  .flexContainer input, .flexContainer textarea {
    color: #ffffff;
    background: none;
    border: none;
    box-sizing: border-box;
    border-bottom: 2px solid #045997;
    padding: 0 5px;
    height: 40px;
    line-height: 40px;
    font-family: inherit;
    transition: 100ms ease-in-out;
    resize: vertical; }
    .flexContainer input::placeholder, .flexContainer textarea::placeholder {
      color: #83caff;
      text-transform: uppercase;
      font-weight: 600;
      font-size: 14px; }
    .flexContainer input:focus, .flexContainer textarea:focus {
      border-color: #16de99; }
  .flexContainer textarea {
    height: 60px;
    min-height: 40px;
    max-height: 300px; }
  .flexContainer .submit {
    background: #ffffff;
    color: #045997;
    font-family: inherit;
    font-weight: 600;
    padding: 0 30px;
    height: 40px;
    width: 200px;
    line-height: 40px;
    text-transform: uppercase;
    border: none;
    border-radius: 3px; }



