@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* basic colors ============================================================= */
/* social colors ============================================================ */
.box {
  text-transform: uppercase;
  display: inline-block;
  color: #ffffff;
  margin: 15px 10px;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  padding: 0 30px;
  border-radius: 5px;
  background-image: linear-gradient(to right, #16de99 0%, #00c8ce 50%, #aa97ec 100%);
  background-size: 200%;
  background-position: left;
  transition: all 300ms ease-in-out;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1), inset 0 0 1px rgba(255, 255, 255, 0.4);
  user-select: none;
  cursor: pointer; }
  .box:hover {
    text-decoration: none;
    background-position: right;
    transform: translateY(-20%);
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1), inset 0 0 1px rgba(255, 255, 255, 0.4);
    color: #ffffff; }
  .box__statical:hover {
    transform: none;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1), inset 0 0 1px rgba(255, 255, 255, 0.4); }



