@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* basic colors ============================================================= */
/* social colors ============================================================ */
.panel {
  padding: 20px 10px;
  display: flex;
  justify-content: flex-end; }
  .panel:first-of-type {
    border-bottom: 1px solid #f2f2f2;
    padding-top: 0; }
  .panel:last-of-type {
    border-top: 1px solid #f2f2f2;
    padding-bottom: 0; }
  .panel span {
    flex-grow: 1; }
  .panel button {
    text-transform: uppercase;
    display: none;
    background: none;
    padding: 5px 15px;
    margin-left: 5px;
    border-radius: 2px;
    transition: all .1s ease-in-out;
    user-select: none; }
    .panel button:hover {
      color: white;
      cursor: pointer; }
    .panel button.active {
      display: inline-block; }
  .panel .add {
    border: 1px solid #16de99;
    color: #16de99; }
    .panel .add:hover {
      background-color: #16de99; }
  .panel .edit {
    border: 1px solid #e4aa38;
    color: #e4aa38; }
    .panel .edit:hover {
      background-color: #e4aa38; }
  .panel .delete {
    border: 1px solid #f64024;
    color: #f64024; }
    .panel .delete:hover {
      background-color: #f64024; }
  .panel .cancel {
    border: 1px solid #697984;
    color: #697984; }
    .panel .cancel:hover {
      background-color: #697984; }



