@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* basic colors ============================================================= */
/* social colors ============================================================ */
.container {
  color: #697984;
  padding: 50px 0; }
  .container h1 {
    color: #084690;
    text-align: center; }

.loading {
  background: url(/img/loader.svg) no-repeat center/contain;
  height: 200px;
  margin-top: 50px; }

.position {
  border-top: 1px solid #e0e0e0;
  padding: 20px;
  color: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 200ms ease-in-out; }
  @media screen and (max-width: 599px) {
    .position {
      display: block; } }
  .position > * + * {
    margin: 0 0 0 40px; }
    @media screen and (min-width: 600px) and (max-width: 999px) {
      .position > * + * {
        margin-left: 20px; } }
    @media screen and (max-width: 599px) {
      .position > * + * {
        margin-left: 0; } }
  .position:first-of-type {
    margin-top: 40px; }
  .position:last-of-type {
    border-bottom: 1px solid #e0e0e0;
    margin-bottom: 40px; }
  .position:hover {
    background-color: #fafafa;
    text-decoration: none; }
  .position .basicInfo {
    position: relative;
    padding-left: 100px;
    flex-grow: 2; }
    @media screen and (min-width: 600px) and (max-width: 999px) {
      .position .basicInfo {
        padding-left: 0; } }
    @media screen and (max-width: 599px) {
      .position .basicInfo {
        padding-left: 70px; } }
    @media screen and (max-width: 599px) {
      .position .basicInfo p {
        margin-bottom: 1em; } }
  @media screen and (max-width: 999px) {
    .position .type {
      float: right;
      margin: 0; } }
  .position .icon {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    height: 60px;
    width: 60px;
    overflow: hidden;
    float: left; }
    @media screen and (min-width: 600px) and (max-width: 999px) {
      .position .icon {
        display: none; } }
  .position .name {
    color: #0059bf;
    font-size: 20px;
    font-weight: 400;
    text-align: left; }
    @media screen and (max-width: 599px) {
      .position .name {
        text-align: left; } }
  .position .apply {
    text-transform: uppercase;
    color: #017dd6;
    border: 1px solid #017dd6;
    background: #ffffff;
    border-radius: 5px;
    height: 50px;
    line-height: 50px;
    font-weight: 600;
    padding: 0 40px;
    font-size: 16px; }
    @media screen and (max-width: 599px) {
      .position .apply {
        display: block;
        text-align: center;
        width: 150px;
        margin: 20px auto 0 auto; } }
  .position .geography {
    position: relative;
    padding-left: 25px;
    white-space: nowrap; }
    .position .geography::before {
      content: '';
      display: block;
      user-select: none;
      pointer-events: none;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 20px;
      height: 25px;
      background: url(/img/geo.svg) no-repeat center/contain; }



