.container {
  list-style: none;
  display: flex !important;
  flex-flow: row nowrap;
  margin: 0;
  padding: 0 20px;
  align-items: center;
  justify-content: flex-end;
  white-space: nowrap; }
  @media screen and (max-width: 599px) {
    .container {
      flex-flow: column nowrap;
      align-items: center;
      justify-content: center;
      z-index: 1000;
      position: fixed;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      background-image: linear-gradient(to right, #0059bf 0%, #00a2f0 100%);
      opacity: 0;
      pointer-events: none;
      transition: all 200ms ease-in-out; } }
  @media screen and (max-width: 599px) {
    .container__open {
      opacity: 1;
      pointer-events: all; } }

.item {
  height: 100%; }
  @media screen and (max-width: 599px) {
    .item {
      margin: 20px 0;
      height: 40px;
      width: 100%;
      text-align: center; } }
  .item a {
    transition: color 400ms ease-in-out;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 20px; }
    @media screen and (max-width: 599px) {
      .item a {
        text-align: center;
        display: block;
        color: #ffffff !important; } }
  .item__active a {
    color: #ffffff; }
    .item__active a:hover {
      text-decoration: none; }



