@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* basic colors ============================================================= */
/* social colors ============================================================ */
.container {
  font-size: 12px;
  background: #222c33; }
  .container a {
    text-transform: none !important; }

.wrapper {
  padding: 6px 20px;
  box-sizing: border-box;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between; }

.dropdown {
  position: relative; }
  .dropdown ul {
    background: #222c33;
    position: absolute;
    right: 0;
    list-style: none;
    padding: 0;
    margin: 0;
    border-top: 6px solid transparent;
    transition: all 200ms ease-in-out;
    border-radius: 0 0 2px 2px;
    opacity: 0;
    pointer-events: none; }
    .dropdown ul > * {
      padding: 6px 20px;
      white-space: nowrap; }
  .dropdown:hover ul {
    opacity: 1;
    pointer-events: all; }

.logout {
  cursor: pointer;
  font-family: inherit;
  background: none;
  padding: 0;
  transition: all 200ms ease-in-out;
  border: none;
  color: inherit; }
  .logout:hover {
    color: #e4aa38; }



