/* social colors ============================================================ */
.container {
  padding-top: 40px;
  color: black;
  display: flex; }
  .container > *:nth-of-type(2) {
    flex-grow: 1; }

.menu {
  border-right: 1px solid #f2f2f2;
  margin: 0 20px 20px 0;
  list-style: none;
  padding: 0 20px 0 0; }
  .menu a {
    display: block;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    color: #697984;
    text-align: center;
    transition: all .2s ease-in-out; }
    .menu a i {
      margin: 0 !important; }
    .menu a:hover {
      background-color: #f2f2f2;
      text-decoration: none; }
    .menu a.active {
      background-color: #084690;
      color: #ffffff; }



